
.section-block {
  padding: 80px 0 35px 0;


  .aside {
    margin-top: 60px;
    padding: 0 15px;


    &__block {
      text-align: left;
      padding: 30px 20px;
      margin-bottom: 65px;
      border: 1px solid #ebf2f3;

      &-icon {
        width: 80px;
        height: 80px;
        border-radius: 6px;
        font-size: 42px;
        color: $footer-h6;
        margin: -76px 0 26px;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0 0 10px 6px rgba(0, 0, 0, 0.03);
        background-color: #fff;

        a {
          font-family: 'Roboto', sans-serif;
          color: $footer-h6;
          transition: .3s;
          font-size: 44px;
          line-height: 1.3;
          font-weight: 600;
        }
      }

      h5 {
        margin-bottom: 10px;

        a {
          color: $footer-h6;
          transition: .3s;

          &:hover {
            color: $footer-logo-color;
          }
        }
      }

      p {
        text-align: left;
        color: $text-color-section;
        font-size: 16px;
        line-height: 1.8;
        transition: .3s;
        font-weight: 400;
      }

      &-link {
        width: 25px;
        height: 25px;
        transition: .3s;
        padding: 0;
        margin: 0;

        span {
          color: $footer-logo-color;
          font-size: 16px;
          transition-delay: 0.5s;

          &:hover {
            display: flex;
            align-items: center;

            &:before {
              margin: 0;
              padding: 0 15px 0 0;
              font-size: 18px;
              font-weight: 600;
              white-space: pre;
              content: "\Read More";
              color: #00464d;

            }
          }

        }
      }
    }
  }
}



