body {
  height: 100%;
}

.top-bar-wrapper {
  position: relative;
  z-index: 100;
  background: $background;
}

.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  color: $white;

  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  a {
    color: #fff;
    text-decoration: none;
  }

  &__mail {

    margin-right: 24px;

    a {
      margin-right: 10px;


    }
  }

  &__address {

    a {
      margin-right: 10px;
    }
  }

  &__social-links {
    a {
      margin-right: 24px;
    }

    a:last-child {
      margin-right: 0;
    }
  }
}

.nav-bar-wrapper {
  position: relative;
  z-index: 100;
  background-color: rgba(0, 89, 99, 0.7);
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: $white;

  &__logo {
    width: 190px;
    padding: 10px 0;

    img {
      width: 100%;

      &:hover {
        cursor: pointer;
      }
    }
  }

  &__btn {

    button {
      width: 162px;
      text-transform: uppercase;
    }
  }
}

.content-menu {

  display: flex;
  justify-content: center;
  align-items: center;
  color: $white;
  background: url("../components/shared/photo-background/home-team-banner.jpg") no-repeat;
  background-position: center top;
  background-size: cover;
  min-height: calc(100vh);
  margin-top: -126px;

  &__title {
    text-align: center;

    h6 {
      font-weight: 600;
    }

    h1 {
      font-weight: 600;
    }
  }

  &__btn {
    display: flex;
    justify-content: center;

    button {

      &:first-child {
        margin-right: 15px;
      }

      &:last-child {
        margin-right: 15px;
      }
    }
  }
}

.checkbox {
  padding: 0;
  margin: 0;
  opacity: 0;
}

@media screen and (max-width: 960px) {

  .content-menu {
    min-height: auto;
    padding: 140px 0 140px;
  }
}

@media screen and (max-width: 470px) {

  .content-menu {

    &__btn {
      flex-direction: column;
      justify-content: center;

      button {

        &:first-child {
          margin-right: 0;
        }

        &:last-child {
          margin-left: 15px ;
          order: -1;
        }
      }
    }
  }
}

@media screen and (min-width: 801px) {
  .menu-burger {
    display: flex;
    align-items: center;

    input {
      display: none;
    }

    .menu ul {
      display: flex;
      margin: 0;

      li {
        list-style-type: none;
        padding: 0;
        cursor: pointer;
        font-weight: 500;
        transition: 0.2s;

        a {
          font-size: 16px;
          text-transform: uppercase;
          font-weight: 500;
          padding: 30px 20px;
          text-decoration: none;
          color: $white;
          transition: 0.3s;

          &:hover {
            cursor: pointer;
            color: $footer-logo-color;
          }
        }
      }

    }
  }

}


@media screen and (max-width: 800px) {

  .top-bar {
    justify-content: center;
  }

  .top-bar__social-links {
    display: none;
  }
}

@media screen and (max-width: 800px) {

  .top-bar {
    display: none;
  }
  .nav-bar__btn {
    display: none;
  }
}

@media screen and (max-width: 800px) {
  .container {
    max-width: 100%;
    margin: auto;
    overflow: hidden;
    padding: 0 3rem;
  }

  .showcase {
    background: var(--primary-color);
    color: #fff;
    height: 100vh;
    position: relative;
  }

  .showcase:before {
    content: '';
    background: url('https://images.pexels.com/photos/533923/pexels-photo-533923.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260') no-repeat center center/cover;
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }

  .showcase .showcase-inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
  }

  .showcase h1 {
    font-size: 4rem;
  }

  .showcase p {
    font-size: 1.3rem;
  }

  .btn {
    display: inline-block;
    border: none;
    background: var(--primary-color);
    color: #fff;
    padding: 0.75rem 1.5rem;
    margin-top: 1rem;
    transition: opacity 1s ease-in-out;
    text-decoration: none;
  }

  .btn:hover {
    opacity: 0.7;
  }

  /* MENU STYLES */
  .menu-wrap {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1;
  }

  .menu-wrap .toggler {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    cursor: pointer;
    width: 50px;
    height: 50px;
    opacity: 0;
  }

  .menu-wrap .hamburger {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    width: 60px;
    height: 60px;
    padding: 1rem;
    background: var(--primary-color);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  /* Hamburger Line */
  .menu-wrap .hamburger > div {
    position: relative;
    flex: none;
    width: 100%;
    height: 2px;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.4s ease;
  }

  /* Hamburger Lines - Top & Bottom */
  .menu-wrap .hamburger > div::before,
  .menu-wrap .hamburger > div::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: -10px;
    width: 100%;
    height: 2px;
    background: inherit;
  }

  /* Moves Line Down */
  .menu-wrap .hamburger > div::after {
    top: 10px;
  }

  /* Toggler Animation */
  .menu-wrap .toggler:checked + .hamburger > div {
    transform: rotate(135deg);
  }

  /* Turns Lines Into X */
  .menu-wrap .toggler:checked + .hamburger > div:before,
  .menu-wrap .toggler:checked + .hamburger > div:after {
    top: 0;
    transform: rotate(90deg);
  }

  /* Rotate On Hover When Checked */
  .menu-wrap .toggler:checked:hover + .hamburger > div {
    transform: rotate(225deg);
  }

  /* Show Menu */
  .menu-wrap .toggler:checked ~ .menu {
    visibility: visible;
  }

  .menu-wrap .toggler:checked ~ .menu > div {
    transform: scale(1);
    transition-duration: var(--menu-speed);
  }

  .menu-wrap .toggler:checked ~ .menu > div > div {
    opacity: 1;
    transition: opacity 0.4s ease 0.4s;
  }

  .menu-wrap .menu {
    position: fixed;
    top: 0;
    right: 0;
    width: 50%;
    height: 90%;
    visibility: hidden;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(26, 26, 26, 0.68);
  }

  .menu-wrap .menu > div {
    background: var(--overlay-color);
    border-radius: 50%;
    width: 200vw;
    height: 200vw;
    display: flex;
    flex: none;
    align-items: center;
    justify-content: center;
    transform: scale(0);
    transition: all 0.4s ease;
  }

  .menu-wrap .menu > div > div {
    text-align: center;
    max-width: 90vw;
    max-height: 100vh;
    opacity: 0;
    transition: opacity 0.4s ease;
  }

  .menu-wrap .menu > div > div > ul > li {
    list-style: none;
    color: #fff;
    font-size: 1.5rem;
    padding: 1rem;
  }

  .menu-wrap .menu > div > div > ul > li > a {
    color: inherit;
    text-decoration: none;
    transition: color 0.4s ease;
  }


}